let ADD_MESSAGE = 'ADD-MESSAGE';



let initialState = {
    message:[
        {text: '–ü–µ—Ä–≤–æ–µ —Å–æ–æ–±—à–µ–Ω–∏–µ'},
        {text: '–í—Ç–æ—Ä–æ–µ —Å–æ–æ–±—à–µ–Ω–∏–µ'},
        {text: '–¢—Ä–µ—Ç–µ–µ —Å–æ–æ–±—à–µ–Ω–∏–µ'},
        {text: '–ß–µ—Ç–≤–µ—Ä—Ç–æ–µ —Å–æ–æ–±—à–µ–Ω–∏–µ'}
    ]


};


const messageReducer = (state = initialState, action) =>{
    switch(action.type){
        case ADD_MESSAGE:
            return{...state,
                message:[...state.message, {text: action.newMessage}]
            };

        default:
            return state;
    }
};




export let addMessageActionCreater = (newMessage)=>{
    return{ type: ADD_MESSAGE, newMessage}
};

export default messageReducer;